@import url(https://fonts.googleapis.com/css?family=Montserrat:400,400i,700);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html,
body {
  height: 100vh;
  margin: 0;
  padding: 0;
  position: relative;
  cursor: none;
  overflow: hidden;
}

.container {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  display: flex;
  justify-content: center;
  align-items: center;
}
.container::before {
  content: "";
  background: red;
  width: 100%;
  height: 0%;
  position: absolute;
  bottom: 0;
  -webkit-transform-origin: bottom;
          transform-origin: bottom;
  -webkit-animation: splash 3s cubic-bezier(0.8, 0.2, 0.2, 0.8) forwards;
          animation: splash 3s cubic-bezier(0.8, 0.2, 0.2, 0.8) forwards;
}

span {
  font-family: "Montserrat";
  text-transform: uppercase;
  font-weight: bold;
  font-size: 5vw;
  color: red;
  letter-spacing: 2rem;
  /*   filter: blur(3px); */
  -webkit-animation: blurMe 1.4s ease-in-out forwards;
          animation: blurMe 1.4s ease-in-out forwards;
  opacity: 0;
}

.end{
  margin-right: 4vw;
}

span:nth-child(2n+1) {
  -webkit-animation-delay: calc(2s + 200ms * 1);
          animation-delay: calc(2s + 200ms * 1);
}

span:nth-child(2n+2) {
  -webkit-animation-delay: calc(2s + 200ms * 2);
          animation-delay: calc(2s + 200ms * 2);
}

span:nth-child(2n+3) {
  -webkit-animation-delay: calc(2s + 200ms * 3);
          animation-delay: calc(2s + 200ms * 3);
}

span:nth-child(2n+4) {
  -webkit-animation-delay: calc(2s + 200ms * 4);
          animation-delay: calc(2s + 200ms * 4);
}

span:nth-child(2n+5) {
  -webkit-animation-delay: calc(2s + 200ms * 5);
          animation-delay: calc(2s + 200ms * 5);
}

span:nth-child(2n+6) {
  -webkit-animation-delay: calc(2s + 200ms * 6);
          animation-delay: calc(2s + 200ms * 6);
}

.cursor {
  position: fixed;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  border: 1px solid red;
  padding: 1rem;
  border-radius: 50%;
  pointer-events: none;
  mix-blend-mode: difference;
  opacity: 0;
  transition: left 60ms linear, top 60ms linear, -webkit-transform 250ms ease;
  transition: transform 250ms ease, left 60ms linear, top 60ms linear;
  transition: transform 250ms ease, left 60ms linear, top 60ms linear, -webkit-transform 250ms ease;
}

.inner-cursor {
  position: fixed;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  background-color: red;
  padding: 1rem;
  border-radius: 50%;
  pointer-events: none;
  opacity: 0;
  mix-blend-mode: difference;
  -webkit-transform-origin: center;
          transform-origin: center;
  transition: left 60ms linear, top 60ms linear, -webkit-transform 250ms ease;
  transition: transform 250ms ease, left 60ms linear, top 60ms linear;
  transition: transform 250ms ease, left 60ms linear, top 60ms linear, -webkit-transform 250ms ease;
  -webkit-animation: revealCursor 1s ease 5s forwards;
          animation: revealCursor 1s ease 5s forwards;
}

span:hover ~ .cursor {
  -webkit-animation: blinkCursor 1s ease infinite;
          animation: blinkCursor 1s ease infinite;
  opacity: 1;
}

span:hover ~ .inner-cursor {
  -webkit-transform: translate(-50%, -50%) scale(1.5);
          transform: translate(-50%, -50%) scale(1.5);
}

@-webkit-keyframes blinkCursor {
  0% {
    -webkit-transform: translate(-50%, -50%) scale(1.5);
            transform: translate(-50%, -50%) scale(1.5);
  }
  50% {
    -webkit-transform: translate(-50%, -50%) scale(2.5);
            transform: translate(-50%, -50%) scale(2.5);
  }
  100% {
    -webkit-transform: translate(-50%, -50%) scale(1.5);
            transform: translate(-50%, -50%) scale(1.5);
  }
}

@keyframes blinkCursor {
  0% {
    -webkit-transform: translate(-50%, -50%) scale(1.5);
            transform: translate(-50%, -50%) scale(1.5);
  }
  50% {
    -webkit-transform: translate(-50%, -50%) scale(2.5);
            transform: translate(-50%, -50%) scale(2.5);
  }
  100% {
    -webkit-transform: translate(-50%, -50%) scale(1.5);
            transform: translate(-50%, -50%) scale(1.5);
  }
}
@-webkit-keyframes blurMe {
  0% {
    -webkit-filter: blur(10px);
            filter: blur(10px);
    opacity: 0;
  }
  100% {
    -webkit-filter: blur(0px);
            filter: blur(0px);
    opacity: 1;
  }
}
@keyframes blurMe {
  0% {
    -webkit-filter: blur(10px);
            filter: blur(10px);
    opacity: 0;
  }
  100% {
    -webkit-filter: blur(0px);
            filter: blur(0px);
    opacity: 1;
  }
}
@-webkit-keyframes splash {
  0% {
    height: 0%;
  }
  50% {
    height: 100%;
  }
  100% {
    top: 0;
    height: 0%;
  }
}
@keyframes splash {
  0% {
    height: 0%;
  }
  50% {
    height: 100%;
  }
  100% {
    top: 0;
    height: 0%;
  }
}
@-webkit-keyframes revealCursor {
  100% {
    opacity: 1;
  }
}
@keyframes revealCursor {
  100% {
    opacity: 1;
  }
}
