@import url("https://fonts.googleapis.com/css?family=Montserrat:400,400i,700");
html,
body {
  height: 100vh;
  margin: 0;
  padding: 0;
  position: relative;
  cursor: none;
  overflow: hidden;
}

.container {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  justify-content: center;
  align-items: center;
}
.container::before {
  content: "";
  background: red;
  width: 100%;
  height: 0%;
  position: absolute;
  bottom: 0;
  transform-origin: bottom;
  animation: splash 3s cubic-bezier(0.8, 0.2, 0.2, 0.8) forwards;
}

span {
  font-family: "Montserrat";
  text-transform: uppercase;
  font-weight: bold;
  font-size: 5vw;
  color: red;
  letter-spacing: 2rem;
  /*   filter: blur(3px); */
  animation: blurMe 1.4s ease-in-out forwards;
  opacity: 0;
}

.end{
  margin-right: 4vw;
}

span:nth-child(2n+1) {
  animation-delay: calc(2s + 200ms * 1);
}

span:nth-child(2n+2) {
  animation-delay: calc(2s + 200ms * 2);
}

span:nth-child(2n+3) {
  animation-delay: calc(2s + 200ms * 3);
}

span:nth-child(2n+4) {
  animation-delay: calc(2s + 200ms * 4);
}

span:nth-child(2n+5) {
  animation-delay: calc(2s + 200ms * 5);
}

span:nth-child(2n+6) {
  animation-delay: calc(2s + 200ms * 6);
}

.cursor {
  position: fixed;
  transform: translate(-50%, -50%);
  border: 1px solid red;
  padding: 1rem;
  border-radius: 50%;
  pointer-events: none;
  mix-blend-mode: difference;
  opacity: 0;
  transition: transform 250ms ease, left 60ms linear, top 60ms linear;
}

.inner-cursor {
  position: fixed;
  transform: translate(-50%, -50%);
  background-color: red;
  padding: 1rem;
  border-radius: 50%;
  pointer-events: none;
  opacity: 0;
  mix-blend-mode: difference;
  transform-origin: center;
  transition: transform 250ms ease, left 60ms linear, top 60ms linear;
  animation: revealCursor 1s ease 5s forwards;
}

span:hover ~ .cursor {
  animation: blinkCursor 1s ease infinite;
  opacity: 1;
}

span:hover ~ .inner-cursor {
  transform: translate(-50%, -50%) scale(1.5);
}

@keyframes blinkCursor {
  0% {
    transform: translate(-50%, -50%) scale(1.5);
  }
  50% {
    transform: translate(-50%, -50%) scale(2.5);
  }
  100% {
    transform: translate(-50%, -50%) scale(1.5);
  }
}
@keyframes blurMe {
  0% {
    filter: blur(10px);
    opacity: 0;
  }
  100% {
    filter: blur(0px);
    opacity: 1;
  }
}
@keyframes splash {
  0% {
    height: 0%;
  }
  50% {
    height: 100%;
  }
  100% {
    top: 0;
    height: 0%;
  }
}
@keyframes revealCursor {
  100% {
    opacity: 1;
  }
}